(function () {
  angular.module('homeServices', []).
    factory('ProxyService', ProxyService).
    factory('GraphService', GraphService);

  function ProxyService ($resource) {

    const proxy = {};

    proxy.resource = $resource('proxy/v1/info/:id', {}, {
      query: {
        method: 'GET',
        // cache: NodeCache,
        isArray: false
      },
      get: {method: 'GET'},
      save: {method: 'POST'},
      update: {
        method: 'PATCH',
        isArray: false
      },
      delete: {method: 'DELETE'}
    });

    // local info storage
    proxy.setInfo = function (data) {
      proxy.info = data;
    };

    proxy.getInfo = function () {
      return proxy.info;
    };

    return proxy;

  }

  function GraphService ($resource) {

    const graph = {};

    graph.graphResource = $resource('data/data.json', {}, {
    //graph.graphResource = $resource('/mgmt/shared/cloudproxy/v1/graph', {}, {
      query: {
        method: 'GET',
        isArray: true
      }
    });

    return graph;
  }

}());
